import { useDispatch, useSelector } from "react-redux";
import { purchaseUpgrade } from "../store";
import { Tooltip, Button } from "@material-tailwind/react";

function Upgrades() {
  const dispatch = useDispatch();
  const upgradeCost = useSelector((state) => {
    return state.cookies.upgradeCost;
  });

  const handleUpgrade = () => {
    dispatch(purchaseUpgrade());
  };

  return (
    <div className="flex flex-col items-center">
      <h1 className="mb-3 font-bold">Upgrade CLICKS!</h1>
      <Tooltip content={`Each upgrade 2x your clicks`} placement="left">
        <Button onClick={handleUpgrade} variant="outlined" style={{ minWidth: '150px', minHeight: '40px' }}>
          Cost: {upgradeCost}
        </Button>
      </Tooltip>
    </div>
  );
}

export default Upgrades;
