import { createSlice } from "@reduxjs/toolkit";
import { getGeneratorRate } from "../../components/GeneratorInfo";

const cookieSlice = createSlice({
  name: "cookies",
  initialState: {
    count: 0,
    clickMultiplier: 1,
    upgrades: [],
    upgradeCost: 20,
    generators: {
      // Add more generator costs as needed
      N: [],
      R: [],
      SR: [],
      SSR: [],
      UR: [],
      "UR+": [],
    },
    generatorCost: {
      // Add more generator costs as needed
      N: 25,
      R: 50,
      SR: 100,
      SSR: 250,
      UR: 500,
      "UR+": 30000,
    },
    cookiesPerSecond: 0,
  },
  reducers: {
    increment(state, action) {
      state.count += 1 * state.clickMultiplier;
    },
    purchaseUpgrade(state, action) {
      if (state.count >= state.upgradeCost) {
        state.upgrades.push({
          id: state.upgrades.length + 1,
          multiplier: state.clickMultiplier * 2,
        });
        state.count -= state.upgradeCost;
        state.clickMultiplier *= 2;
        state.upgradeCost *= 2;
      }
    },
    purchaseGenerator(state, action) {
      const generatorType = action.payload;
      const cost = state.generatorCost[generatorType];
      if (state.count >= cost) {
        const generatorRate = getGeneratorRate(generatorType);
        state.generators[generatorType].push({
          id: state.generators[generatorType].length + 1,
          rate: generatorRate,
        });
        state.count -= cost;
        state.cookiesPerSecond += generatorRate;
        state.generatorCost[generatorType] *= 1.25;
        // Round the result
        state.generatorCost[generatorType] = Math.round(state.generatorCost[generatorType]);
      }
    },
    incrementCookies(state, action) {
      state.count += state.cookiesPerSecond / 5;
      state.count = Math.round(state.count); 
    }
  },
});

export const {
  increment,
  purchaseUpgrade,
  purchaseGenerator,
  incrementCookies,
} = cookieSlice.actions;
export const cookiesReducer = cookieSlice.reducer;
