import { Tooltip, Button } from "@material-tailwind/react";
import { useDispatch, useSelector } from "react-redux";
import { purchaseGenerator } from "../store";
import { getGeneratorRate, getGeneratorTypeName } from "./GeneratorInfo";

const generatorTypes = ["N", "R", "SR", "SSR", "UR", "UR+"];

function Generator() {
  const dispatch = useDispatch();
  const generatorCost = useSelector((state) => {
    return state.cookies.generatorCost;
  });

  const handleGeneratorPurchase = (type) => {
    dispatch(purchaseGenerator(type));
  };

  const generatorButtons = generatorTypes.map((type) => {
    return (
        <Tooltip
          content={`
       ${getGeneratorTypeName(type)} Generator:
        Produces ${getGeneratorRate(type)} ${
            type === "N" ? "cookie" : "cookies"
          } per second
        `}
          placement="left"
        >
          <Button
            onClick={() => {
              handleGeneratorPurchase(type);
            }}
            variant="outlined"
            style={{ minWidth: "150px", minHeight: "40px" }}
          >
            {type}<br /> Cost: {generatorCost[type]}
          </Button>
        </Tooltip>
    );
  });

  return (
    <div className="flex flex-col w-max gap-4 p-5 items-center">
      <p className="font-bold">Cookies Generator</p>
      {generatorButtons}
    </div>
  );
}

export default Generator;
