import { useEffect, useState } from "react";

function FloatingNumber({ value, x, y }) {
  const [opacity, setOpacity] = useState(1);

  useEffect(() => {
    const timer = setInterval(() => {
      setOpacity((oldOpacity) => oldOpacity - 0.1);
    }, 100); 

    return () => clearInterval(timer);
  }, []);

  return (
    <div
      style={{ left: x, top: y, opacity }}
      className="floating-number absolute animate-float text-center z-[1] font-bold text-white text-2xl pointer-events-none"
    >
      +{value}
    </div>
  );
}

export default FloatingNumber;