import cookieImage from "../images/cookie.png";
import { useSelector, useDispatch } from "react-redux";
import { increment, incrementCookies } from "../store";
import { useEffect, useState } from "react";
import FloatingNumber from "./FloatingNumbers";
import Panel from "./Panel";

function Cookie() {
  const [isClicked, setIsClicked] = useState(false);
  const [clicks, setClicks] = useState([]);
  const dispatch = useDispatch();
  const { count, cookiesPerSecond, clickMultiplier } = useSelector((state) => {
    return {
      count: state.cookies.count,
      cookiesPerSecond: state.cookies.cookiesPerSecond,
      clickMultiplier: state.cookies.clickMultiplier,
    };
  });

  // For clicks (How long it stays on the screen)
  useEffect(() => {
    const timer = setInterval(() => {
      setClicks((oldClicks) => oldClicks.slice(1));
    }, 1000);

    return () => clearInterval(timer);
  }, []);

  // Update for Cookies/s
  useEffect(() => {
    const interval = setInterval(() => {
      dispatch(incrementCookies());
    }, 200);

    return () => clearInterval(interval);
  }, [dispatch]);

  const handleClick = (event) => {
    const rect = event.target
      .closest(".cookie-container")
      .getBoundingClientRect();
    const x = event.clientX - rect.left;
    const y = event.clientY - rect.top;
    setClicks((oldClicks) => [
      ...oldClicks,
      { id: Date.now(), value: clickMultiplier, x, y },
    ]);
    dispatch(increment());
  };

  const handleMouseDown = () => {
    setIsClicked(true);
  };

  const handleMouseUp = () => {
    setIsClicked(false);
  };

  // Prevent Dragging of Cookie
  const handleDragStart = (event) => {
    event.preventDefault();
  };

  return (
    <div className="flex flex-col items-center gap-5">
      <div className="p-10 text-2xl">
        <Panel className="flex flex-col p-10 items-center gap-3">
          <p>{count} Cookies</p>
          <p> per second {cookiesPerSecond}</p>
        </Panel>
      </div>
      <div className="relative cookie-container w-full h-full flex justify-center items-center">
        {clicks.map((click) => (
          <FloatingNumber
            key={click.id}
            value={click.value}
            x={click.x}
            y={click.y}
          />
        ))}
        <div
          onMouseDown={handleMouseDown}
          onMouseUp={handleMouseUp}
          onClick={handleClick}
          onDragStart={handleDragStart}
          className={`${isClicked ? "scale-95" : "scale-100"} w-2/5 min-w-2/5`}
        >
          <img
            className="w-full h-full animate-spin cursor-pointer"
            src={cookieImage}
            alt="cookie"
          />
        </div>
      </div>
    </div>
  );
}

export default Cookie;
