import { configureStore } from "@reduxjs/toolkit";
import {
  cookiesReducer,
  increment,
  purchaseGenerator,
  purchaseUpgrade,
  incrementCookies,
} from "./slices/cookiesSlice";
import { persistReducer, persistStore } from "redux-persist";
import storage from "redux-persist/lib/storage";

const persistConfig = {
  key: "root",
  storage,
};

const persistedReducer = persistReducer(
  persistConfig,
  cookiesReducer
);

const store = configureStore({
  reducer: {
    cookies: persistedReducer,
  },
});

let persistor = persistStore(store);

export {
  store,
  persistor,
  increment,
  purchaseGenerator,
  purchaseUpgrade,
  incrementCookies,
};