import { useSelector } from "react-redux";
import Panel from "./Panel";

function PurchasesSummary() {
  const { upgradesCount, generators, clickMultiplier } = useSelector(
    (state) => {
      return {
        upgradesCount: state.cookies.upgrades.length,
        generators: state.cookies.generators,
        clickMultiplier: state.cookies.clickMultiplier,
      };
    }
  );

  return (
    <Panel className="flex flex-row gap-20 font-bold justify-center">
      <div>
        <p>Upgrades Purchased: {upgradesCount}</p>
        <p>Current Click Multiplier: {clickMultiplier}</p>
      </div>
      <div>
        {Object.entries(generators).map(([type, generatorList]) => (
          <p key={type}>
            {type} Generators Purchased: {generatorList.length}
          </p>
        ))}
      </div>
      <div>
      {Object.entries(generators).map(([type, generatorList]) => {
          const totalRate = generatorList.reduce((acc, generator) => acc + generator.rate, 0);
          return (
            <p key={type}>
              Total {type} Generation Rate: {totalRate} CpS
            </p>
          )
        })}
      </div>
    </Panel>
  );
}

export default PurchasesSummary;
