function getGeneratorRate(generatorType) {
  switch (generatorType) {
    case "N":
      return 3;
    case "R":
      return 5;
    case "SR":
      return 8;
    case "SSR":
      return 20;
    case "UR":
      return 50;
    case "UR+":
      return 10000;
    default:
      return 0;
  }
}

function getGeneratorTypeName(generatorType) {
  switch (generatorType) {
    case "N":
      return "Normal";
    case "R":
      return "Rare";
    case "SR":
      return "Super Rare";
    case "SSR":
      return "Super Super Rare";
    case "UR":
      return "Ultra Rare";
    case "UR+":
      return "Ultra Rare Plus";
    default:
      return "";
  }
}

export { getGeneratorRate, getGeneratorTypeName };
